<template>
  <div class="chat-window">
    <div class="chat-header">
      <h2 class="chat-header-title">{{ selectedUserName }}</h2>
    </div>
    <div class="messages">
      <div v-for="(message, index) in messages" :key="index" class="message">
        <div class="message-sender">{{ message.sender }}</div>
        <div class="message-text">{{ message.text }}</div>
      </div>
    </div>
    <div class="input-box">
      <input v-model="newMessage" @keyup.enter="sendMessage" type="text" placeholder="–í–≤–µ–¥–∏—Ç–µ —Å–æ–æ–±—â–µ–Ω–∏–µ...">
      <button @click="sendMessage" class="send-button">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</button>
    </div>
  </div>
</template>

<script>
export default {
  props: ['currentUser', 'selectedUserId', 'users'],
  data() {
    return {
      newMessage: ''
    };
  },
  computed: {
    selectedUserName() {
      const user = this.users.find(user => user.id === this.selectedUserId);
      return user ? user.name : '';
    },
    messages() {
      const user = this.users.find(user => user.id === this.selectedUserId);
      return user ? user.messages : [];
    }
  },
  created() {
    this.channel = new BroadcastChannel('chat_channel');
    this.channel.onmessage = event => {
      const data = event.data;
      if (data.type === 'message' && data.userId === this.selectedUserId) {
        this.users.find(user => user.id === this.selectedUserId).messages.push({ sender: data.sender, text: data.message });
      }
    };
  },
  methods: {
    sendMessage() {
      if (this.newMessage.trim() !== '') {
        this.channel.postMessage({
          type: 'message',
          userId: this.selectedUserId, // –∏—Å–ø–æ–ª—å–∑—É–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–π userId
          sender: this.currentUser,
          message: this.newMessage
        });
        this.users.find(user => user.id === this.selectedUserId).messages.push({ sender: this.currentUser, text: this.newMessage });
        this.newMessage = '';
      }
    }
  }
};
</script>


