<template>
  <div class="chat-list-container">
    <div class="chat-list">
      <h2 class="chat-list-title">–°–ø–∏—Å–æ–∫ —á–∞—Ç–æ–≤:</h2>
      <ul>
        <li v-for="user in users" :key="user.id" @click="selectUser(user.id)" class="chat-list-item">
          <span class="user-icon">üë§</span> {{ user.name }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ['users'],
  created() {
    this.channel = new BroadcastChannel('chat_channel');
    this.channel.onmessage = event => {
      const data = event.data;
      if (data.type === 'selectUser') {
        // –û–±—Ä–∞–±–æ—Ç–∫–∞ –≤—ã–±–æ—Ä–∞ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
        this.$emit('userSelected', data.userId);
        localStorage.setItem('selectedUserId', data.userId); // –°–æ—Ö—Ä–∞–Ω—è–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–π userId –≤ localStorage
      }
    };
  },
  methods: {
    selectUser(userId) {
      this.$emit('userSelected', userId);
      this.channel.postMessage({ type: 'selectUser', userId });
      localStorage.setItem('selectedUserId', userId); // –°–æ—Ö—Ä–∞–Ω—è–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–π userId –≤ localStorage
    }
  }
};
</script>

